<template>
    <div class="wrap">

        <!--  -->
        <AppHeader />

        <div class="inner">
            <div class="location">
                <ul>
                    <li></li>
                    <li></li>
                </ul>
            </div>

            <div class="title">
                <h1></h1>
            </div>

            <div class="aSide">
                <div class="countBox">
                    <span></span>
                    <span class="count"></span>
                </div>

                <div class="selectBox">
                    <div class="inputBox">
                        <input checked="checked" type="checkbox" name="" id="">
                        <span></span>
                    </div>

                    <p class="seldelBtn"></p>
                </div>

                <div class="productBox" v-bind:key="i" v-for="(dbcartVo, i) in cList">
                    <input checked="checked" type="checkbox" name="" id="">
                    <img  v-bind:src="`${this.$store.state.apiBaseUrl}/upload/${dbcartVo.saveName}`">
                    <p class="pName">{{ dbcartVo.productName }}</p>
                    <div class="PnMBtn" v-on:click="cCountUpdate(dbcartVo.productNo, dbcartVo.cCount)">
                        <span class="minus" v-on:click="dbcartVo.cCount -= 1">-</span>
                        <span class="p-count">{{ dbcartVo.cCount }}</span>
                        <span class="plus" v-on:click="dbcartVo.cCount += 1">+</span>
                    </div>
                    <b class="p-charge">{{ (dbcartVo.price * dbcartVo.cCount).toLocaleString('ko-KR') }}</b>
                    <p class="p-delBtn" v-on:click="cartDelete(dbcartVo.productNo)">X</p>
                </div>
                <div class="order-charge"> <span>{{ this.oderPrice.toLocaleString('ko-KR') }}</span></div>


            </div><!-- //aSide -->

            <div class="bSide">
                <div class="b-top">
                    <h2></h2>
                    <div class="totalPrice">
                        <p> </p>
                        <p><b>{{ this.oderPrice.toLocaleString('ko-KR') }}</b></p>
                    </div>
                    <div class="ifBox" v-if="this.oderPrice < 30000">
                        <div>
                            <p> </p>
                            <p><b>3,000</b></p>
                        </div>
                        <div class="final-charge">
                            <b></b>
                            <b>{{ (this.oderPrice + 3000).toLocaleString('ko-KR') }}</b>
                        </div>
                    </div>
                    <div class="ifBox" v-else>
                        <div>
                            <p> </p>
                            <p><b>0</b></p>
                        </div>
                        <div class="final-charge">
                            <b></b>
                            <b>{{ this.oderPrice.toLocaleString('ko-KR') }}</b>
                        </div>
                    </div>
                    <div class="delivery-text">
                        <ul>
                            <li>       30,000    <br> .</li>
                            <li>         .</li>
                        </ul>
                    </div>
                </div>
                <button v-if="this.oderPrice < 30000" v-on:click="goToOrderForm" class="orderBtn" type="submit">{{ (this.oderPrice + 3000).toLocaleString('ko-KR') }}
                    </button>
                <button v-else v-on:click="goToOrderForm" class="orderBtn" type="submit">{{ this.oderPrice.toLocaleString('ko-KR') }}
                </button>
            </div><!-- //bSid -->
        </div><!-- //inner -->

        <!--  -->
        <AppFooter />

    </div><!-- //wrap -->
</template>
<script>
import '@/assets/css/cart/cart.css'
import AppHeader from '@/components/AppHeader.vue'
import AppFooter from '@/components/AppFooter.vue'

import axios from 'axios';

export default {
    name: "CartView",
    components: {
        AppHeader,
        AppFooter
    },
    data() {
        return {
            cList: [],
            cartVo: {
                saveName: "",
                productName: "",
                cCount: "",
                price: ""
            },
            oderPrice: "",
        };
    },
    methods: {
        //  
        getCartList() {
            console.log(" ");

            axios({
                method: 'get', // put, post, delete
                url: `${this.$store.state.apiBaseUrl}/api/cart/list`,
                headers: { "Content-Type": "application/json; charset=utf-8" }, //
                params: {no: this.$store.state.authUser.userNo}, //get   
                //data: cList, //put, post, delete   JSON  
                responseType: 'json' //
            }).then(response => {
                console.log(response); //
                console.log(this.$store.state.authUser.userNo);
                this.cList = response.data.apiData;
                console.log(this.cList);

                this.calculate();

            }).catch(error => {
                console.log(error);
            });
        },
        //  
        calculate() {
            let oderPrice = 0;
            for (let i = 0; i < this.cList.length; i++) {
                oderPrice += (this.cList[i].price*this.cList[i].cCount);
            }
            this.oderPrice = oderPrice;
        },
        //  
        cartDelete(productNo) {
            console.log("  ");

            console.log(productNo);

            axios({
                method: 'delete', // put, post, delete
                url: `${this.$store.state.apiBaseUrl}/api/cart/list`,
                headers: { "Content-Type": "application/json" }, //
                //params: guestbookVo, //get   
                data: { productNo: productNo }, //put, post, delete   JSON  
                responseType: 'json' //
            }).then(response => {
                console.log(response); //

                this.getCartList();

            }).catch(error => {
                console.log(error);
            });
        },
        cCountUpdate(productNo, cCount) {
            console.log("");
            console.log(productNo);
            console.log(cCount);

            axios({
                method: 'post', // put, post, delete
                url: `${this.$store.state.apiBaseUrl}/api/cart/list`,
                headers: { "Content-Type": "application/json" }, //
                //params: guestbookVo, //get   
                data: { productNo: productNo, cCount: cCount }, //put, post, delete   JSON  
                responseType: 'json' //
            }).then(response => {
                console.log(response); //

                this.getCartList();

            }).catch(error => {
                console.log(error);
            });

        },
        goToOrderForm() {
            this.$router.push("/order");
        },
    },
    created() {
        this.getCartList();
    }
};
</script>
<style></style>