<template>
    <div>
        <div class="wrap">
            <!--  -->
            <AppHeader/>

            <!-- container  -->
            <div class="MpOrderContainer">
                <!--  -->
                <div class="MpLeft">
                    <!--nav-->
                    <div class="sidetop">
                        <h2></h2>
                        <ul class="mypageMenu">
                            <li></li>
                            <li><router-link to="/mypage"></router-link></li>
                            <li></li>
                            <li><router-link to="/modify"></router-link></li>
                        </ul>
                    </div>
                    <div class="sidebottom">
                        <img src="@/assets/images/mypage_banner2.png" alt="">
                    </div>
                </div>
                <!--  -->
                <div class="MpRight">
                    <!-- about -->
                    <div class="MpRightAbout">
                        <!--  -->
                        <div class="orderCheckBox">
                            <h1>/</h1>
                            <label for="date">
                                <input type="date" id="" max="2024-12-30" min="2024-01-01"  v-model="selectedDate" v-on:change="dateChange()">
                            </label>
                        </div>
                        <!--   -->
                        <div class="orderListByDate">

                            <div class="orderByDate" v-for="(orderedVo,i) in orderedList" v-bind:key="i"> 
                                <p class="orderDay">{{orderedVo.payDay}}</p>
                                <div class="ordercomplete" >
                                    <img v-bind:src="`${this.$store.state.apiBaseUrl}/upload/${orderedVo.saveName}`" class="orderCompleteImg">
                                    <!-- <img v-bind:src="`${this.$store.state.apiBaseUrl}/upload/${this.$route.query.saveName}`" class="orderCompleteImg"> -->
                                    <div class="buy">
                                        <p><strong>:</strong>{{ orderedVo.productName }}</p>
                                        <p><strong>:</strong> {{ orderedVo.oPrice.toLocaleString() }}</p>
                                        <p><strong>:</strong>{{ orderedVo.payment }} </p>
                                    </div>
                                </div>
                            </div>

                        </div>
                        
                        
                    </div>
                </div>
            </div>

            <!--  -->
            <AppFooter/>
        </div>
    </div>
</template>

<script>
import axios from 'axios';
import "@/assets/css/mypage/MyPageOrder.css"
import AppHeader from '@/components/AppHeader.vue'
import AppFooter from '@/components/AppFooter.vue'

export default {
    name: "MyPageOrder",
    components: {
        AppHeader,
        AppFooter
    },
    data() {
        return {
            no: this.$store.state.authUser.userNo,
            orderedList:[],
            selectedDate: new Date().toISOString().substring(0, 10),
        };
    },
    methods: {
        getOrderedList(){
            console.log("/ ")
            axios({
                method: 'get', // put, post, delete 
                url: `${this.$store.state.apiBaseUrl}/api/orderedListByUserNo`,
                headers: { "Content-Type": "application/json; charset=utf-8" }, //
                params: {no:this.no}, //get   
                // data: guestbookVo, //put, post, delete   JSON  
                responseType: 'json' //
            }).then(response => {
                console.log(response.data.apiData); //
                this.orderedList = response.data.apiData;

                
            }).catch(error => {
                console.log(error);
            });
            

        },
        dateChange() {
            
            console.log("");
            console.log(this.selectedDate);
            
            axios({
                method: 'post',  //put,post,delete
                url: `${this.$store.state.apiBaseUrl}/api/findByDate`,
                headers: { "Content-Type": "application/json; charset=utf-8" }, //
                params: { selectedDate: this.selectedDate}, //get   
                // data: attentionVo, //put, post, delete   JSON  

                responseType: 'json' //
            }).then(response => {
                console.log(response.data); //
                console.log(response.data.apiData); //
                this.orderedList = response.data.apiData;

            }).catch(error => {
                console.log(error);
            });


        },
    },
    created(){
        this.getOrderedList();
    }
};
</script>
