<template>
    <div class="wrap">
        <!--  -->
        <AppHeader />

        <!-- container  -->
        <div class="MpOrderContainer">
            <!--  -->
            <div class="MpLeft">
                <!--nav-->
                <div class="sidetop">
                    <h2></h2>
                    <ul class="mypageMenu">
                        <li></li>
                        <li><router-link to="/mypage"></router-link></li>
                        <li></li>
                        <li><router-link to="/modify"></router-link></li>
                    </ul>
                </div>
                <div class="sidebottom">
                    <img src="@/assets/images/mypage_banner2.png" alt="">
                </div>
            </div>
            <!--  -->
            <div class="MpRight ">


                <h2 class="mbModifytxt">  </h2>
                <div class="advImgBox">
                    <form v-on:submit.prevent="modifyMember" method="put">
                        <div class="id-group">
                            <label class="id" for="id"></label>
                            <span id="fixid">{{ usersVo.id }}</span>
                        </div>
                        <div class="form-blank">
                            <label for="password">*</label>
                            <input type="password" id="password" v-model="usersVo.password" required>
                        </div>
                        <div class="form-blank">
                            <label for="hp"> *</label>
                            <input type="hp" id="hp" v-model="usersVo.hp" required>
                        </div>
                        <div class="eform-blank">
                            <label for="email">*</label>
                            <input type="text" id="email" v-model="mfemailName" required>
                            <select class="domainbox" id="domain-list" v-model="mfDomain">
                                <option value="@naver.com">naver.com</option>
                                <option value="@gmail.com">gmail.com</option>
                                <option value="@hanmail.net">hanmail.net</option>
                                <option value="@nate.com">nate.com</option>
                                <option value="@kakao.com">kakao.com</option>
                                <option value="@msn.com">msn.com</option>
                            </select>
                        </div>
                        <div class="form-blank">
                            <label class="address" for="address"></label>
                            <textarea class="addressbox" v-model="usersVo.address"></textarea>
                            <button type="button" class="postal" v-on:click="postalCode"></button>
                        </div>

                        <button type="submit" class="mdfBtn" v-on:click="updateInfo"></button>
                    </form>
                    <img class="advImg" src="@/assets/images/goodsbanner.png" alt="">


                </div>
            </div>


        </div>

        <!--  -->
        <AppFooter />
    </div>
</template>

<script>
import axios from "axios";
import "@/assets/css/mypage/MyPageOrder.css"
import "@/assets/css/mypage/modifyform.css"
import AppFooter from '@/components/AppFooter.vue';
import AppHeader from '@/components/AppHeader.vue';
export default {
    name: "ModifyFormView",
    components: {
        AppFooter,
        AppHeader
    },
    data() {
        return {
            mfemailName: '',
            mfDomain: '',
            usersVo: {
                id: '',
                password: '',
                hp: '',
                email: '',
                address: ''
            }
        };
    },
    methods: {
        modifyform() {
            console.log("modifyForm");
            console.log(this.usersVo);
            // 
            axios({
                method: 'get', // put, post, delete  // GET // POST
                url: `${this.$store.state.apiBaseUrl}/api/user/modify`, //''  
                headers: {
                    "Content-Type": "application/json; charset=utf-8"
                    , "Authorization": "Bearer " + this.$store.state.token
                },
                //params: guestbookVo, //get    @ModelAttribute
                data: this.usersVo.userNo, //put, post, delete   JSON   @RequestBody

                responseType: 'json' //
            }).then(response => {
                console.log(response.data.apiData); //

                this.usersVo = response.data.apiData;
                this.mfemailName = response.data.apiData.email.split('@')[0];
                this.mfDomain = '@'+response.data.apiData.email.split('@')[1];


            }).catch(error => {
                console.log(error);
            });

        },
        modifyMember() {
            console.log("");
            console.log(this.usersVo);
            this.usersVo.email = this.mfemailName + this.mfDomain;
            // 
            axios({
                method: 'put', // put, post, delete  // GET // POST
                url: `${this.$store.state.apiBaseUrl}/api/user/modify`, //''  
                headers: {
                    "Content-Type": "application/json; charset=utf-8"
                    , "Authorization": "Bearer " + this.$store.state.token
                },
                //params: guestbookVo, 
                data: this.usersVo, //put, post, delete   JSON   @RequestBody

                responseType: 'json' //
            }).then(response => {
                console.log(response); //
                console.log(response.data.apiData)

                if (response.data.result == "success") {
                    console.log("result: success");
                    let name = response.data.apiData.name;

                    //vuex  
                    this.$store.commit("setAuthName", name);

                    // 
                    this.$router.push("/");
                    alert(" ");
                } else {
                    console.log(response.data.message);
                    this.$store.commit("setAuthUser", null);
                    this.$store.commit("setToken", null);
                }


            }).catch(error => {
                console.log(error);
            });
        },
    },
    created() {
        this.modifyform();
    }
};
</script>