<template>
    <div>
        <div class="wrap">
            <!--  -->
            <AppHeader/>

            <!--  -->
            <div class="ItemAccountContainer">
                <div class="ItemAccountInner">
                    <!--  -->
                    <div class="channelSrc">
                        <router-link to="/"></router-link>
                        <router-link to="">//</router-link>
                        <router-link to=""></router-link>
                    </div>

                    <!--  -->
                    <div class="ItemAccountSection">
                        <!--  -  -->
                        <div class="ItemAccountLeft">
                            <img v-bind:src="`${this.$store.state.apiBaseUrl}/upload/${itemInfo.saveName}`">
                            <!-- <img v-bind:src="`${this.$store.state.apiBaseUrl}/upload/${this.$route.query.saveName}`"> -->
                        </div>
                        <!--  -  -->
                        <div class="ItemAccountRight">
                            <div class="ItemAcocuntText">
                                <!-- -Top -->
                                <div class="ItemAcocuntTextTop">
                                    <p>{{ itemInfo.productName }}</p>
                                    <p v-if="itemInfo && itemInfo.price">{{itemInfo.price.toLocaleString()}}<span></span></p>
                                </div>
                                <!-- -Mid -->
                                <ul class="ItemAcocuntTextMid">
                                    <li>
                                        <span></span>
                                        <p>  </p>
                                    </li>
                                    <li>
                                        <span></span>
                                        <p>{{ itemInfo.detail }}</p>
                                    </li>
                                    <li>
                                        <span>&<br></span>
                                        <p>
                                            <span>3,000</span>
                                            <span v-if="this.itemInfo.storage ==1">&</span>
                                            <span v-if="this.itemInfo.storage ==2"></span>
                                            <br>
                                            <span>   30,000   </span>
                                        </p>
                                        
                                    </li>
                                    <li>
                                        <span></span>
                                        <div class="InputWrapper">
                                            <button type="button" class="minusBtn" v-on:click="minusBtn()">-</button>
                                            <input type="text" class="countInput" v-model="goCartVo.cCount">
                                            <button type="button" class="plusBtn" v-on:click="plusBtn()">+</button>

                                        </div>
                                    </li>
                                </ul>
                                <!-- -Bottom -->
                                <div class="ItemAcocuntTextBottom">
                                    <!--  -->
                                    <div class="ItemAcocuntTextBottomPrice">
                                        <p>
                                            <strong>{{ (itemInfo.price * goCartVo.cCount).toLocaleString() }}</strong>
                                            <span></span>
                                        </p>
                                    </div>
                                    <!--  -->
                                    <div class="ItemAcocuntTextBottomBtn">
                                        <router-link to="" v-on:click="modalOpen" ></router-link>
                                        <router-link to="" v-on:click="goOrder()"></router-link>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>

            <!--  -->
            <AppFooter/>


        </div>
        <!-- ========  ======== -->
        <div class="ItemAccountModal" v-bind:class="{'modal-on': isMaodal}">
            <!--  -->
            <div class="ItemAccountModal-content" v-if="this.$store.state.authUser !== null">
                <div class="closeModal" v-on:click="modalClose">x</div>
                <div class="ItemModalText">
                    <p> ?</p>
                </div>
                <div class="ItemModalBtnBox">
                    <router-link to="" v-on:click="goCartTo()"> </router-link>
                    <button v-on:click="goCart()"> </button>
                </div>
            </div>
            <!--   -->
            <div class="ItemAccountModal-content" v-if="this.$store.state.authUser == null">
                <div class="ItemModalText">
                    <p>  .</p>
                </div>
                <div class="ItemModalBtnBox">
                    <router-link to="/login">  </router-link>
                    <button v-on:click="modalClose" ></button>
                </div>
            </div>
        

        </div>
    </div>
 </template>
<script>
import axios from 'axios';
import '@/assets/css/item/itemAccount.css'
import AppHeader from '@/components/AppHeader.vue'
import AppFooter from '@/components/AppFooter.vue'
export default {
    name: "itemAccount",
    components: {
        AppHeader,
        AppFooter
    },
    data() {
        return {
            itemInfo:[],
            goCartVo:{
                cCount:1,
                productNo: this.$route.params.no,
                userNo: null
            }
        };
    },
    methods: {
        //    
        getItemInfo(){
            // console.log(this.no);

            axios({
                method: 'get', // put, post, delete 
                url: `${this.$store.state.apiBaseUrl}/api/itemInfoBypNo`,
                headers: { "Content-Type": "application/json; charset=utf-8" }, //
                params: {no:this.goCartVo.productNo}, //get   
                // data: guestbookVo, //put, post, delete   JSON  
                responseType: 'json' //
            }).then(response => {
                console.log(response.data.apiData); //
                this.itemInfo = response.data.apiData;

                
            }).catch(error => {
                console.log(error);
            });
        },
        //   ->    !
         goCart(){
            this.goCartVo.userNo= this.$store.state.authUser.userNo
            axios({
                method: 'post', // put, post, delete 
                url: `${this.$store.state.apiBaseUrl}/api/goCart`,
                headers: { "Content-Type": "application/json; charset=utf-8" }, //
                data: this.goCartVo , //put, post, delete   JSON  
                responseType: 'json' //
            }).then(response => {
                console.log(response.data.apiData); //
                console.log("  ")

                this.$router.push("/cart")
                
            }).catch(error => {
                console.log(error);
            });
        },
        //   ->     !
        goCartTo(){
            this.goCartVo.userNo= this.$store.state.authUser.userNo
            axios({
                method: 'post', // put, post, delete 
                url: `${this.$store.state.apiBaseUrl}/api/goCart`,
                headers: { "Content-Type": "application/json; charset=utf-8" }, //
                data: this.goCartVo , //put, post, delete   JSON  
                responseType: 'json' //
            }).then(response => {
                console.log(response.data.apiData); //
                console.log("  ")

                this.$router.push("/itemlist/1")
                
            }).catch(error => {
                console.log(error);
            });
        },
        //   ->    !
        goOrder(){
            console.log(" ")
            if(this.$store.state.authUser == null){
                this.modalOpen()
                
            }else{
                this.goCartVo.userNo= this.$store.state.authUser.userNo
                axios({
                    method: 'post', // put, post, delete 
                    url: `${this.$store.state.apiBaseUrl}/api/goCart`,
                    headers: { "Content-Type": "application/json; charset=utf-8" }, //
                    data: this.goCartVo , //put, post, delete   JSON  
                    responseType: 'json' //
                }).then(response => {
                    console.log(response.data.apiData); //
                    console.log("  ")

                    this.$router.push("/order")
                    
                }).catch(error => {
                    console.log(error);
                });
            }
        },
        //    
        modalOpen(){
            // this.isMaodal= true
            document.querySelector('.ItemAccountModal').style.display = "block"
        },
        modalClose(){
            document.querySelector('.ItemAccountModal').style.display = "none"
        },
        // 
        plusBtn(){
            console.log("")
            this.goCartVo.cCount++
        },
        // 
        minusBtn(){
            console.log("")
            if(this.goCartVo.cCount>1){
                this.goCartVo.cCount--
            }
        },
       
        
    },
    created(){
        this.getItemInfo();
    }
};
</script>
<style></style>