<template>
    <AppHeader />
    <!-- //header -->
    <div class="signupForm">
        <h2></h2>
        <form v-on:submit.prevent="signup" action="">
            <h3></h3>
            <hr />
            <div class="form-field">
                <label for="id">*</label>
                <input type="text" id="id" v-model="usersVo.id">
                <button class="dvcheck" v-on:click="idCheck" type="button"></button><br>
            </div>
            <div class="form-field">
                <label for="password">*</label>
                <input type="password" id="password" v-model="usersVo.password" required>
            </div>
            <div class="form-field">
                <label for="name">*</label>
                <input type="text" id="name" v-model="usersVo.userName" required>
            </div>
            <div class="form-field">
                <label for="phone">*</label>
                <input type="tel" id="phone" v-model="usersVo.hp" required>
            </div>
            <div class="eform-field">
                <label for="email">*</label>
                <input type="text" id="email" v-model="emailName">
                <select class="domainbox" id="domain-list" v-model="domain">
                    <option value="@naver.com">@naver.com</option>
                    <option value="@gmail.com">@gmail.com</option>
                    <option value="@hanmail.net">@hanmail.net</option>
                    <option value="@nate.com">@nate.com</option>
                    <option value="@kakao.com">@kakao.com</option>
                    <option value="@msn.com">@msn.com</option>
                </select>
            </div>

            <h3></h3>
            <hr />
            <div class="radioGender">
                <label for="gender" id="gender"></label>
                <input type="radio" name="gender" id="male" value="Male" v-model="usersVo.gender">
                <input type="radio" name="gender" id="female" value="Female" v-model="usersVo.gender">
            </div>
            <div class="form-field">
                <label class="address" for="address"></label>
                <textarea class="addressbox" v-model="usersVo.address"></textarea>
                <button type="button" v-on:click="postalCode"></button>
            </div>
            <div class="form-field">
                <label for="birthdate"></label>
                <input type="date" id="birthdate" v-model="usersVo.birth">
            </div>



            <button type="submit" class="submitForm"></button>
        </form>
    </div>
    <AppFooter />
    <!--//footer-->
</template>

<script>
import axios from "axios";
import "@/assets/css/user/loginform.css"
import AppFooter from '@/components/AppFooter.vue';
import AppHeader from '@/components/AppHeader.vue';
export default {
    name: "LoginFormView",
    components: {
        AppFooter,
        AppHeader
    },
    data() {
        return {
            emailName: "",//    
            domain:"",//  
            usersVo: {
                id: "",
                password: "",
                userName: "",
                phone: "",
                email: "",
                gender: "",
                address: "",
                birth: ""

            },
            check: 0,
            isDuplicated: false,
            isNotDuplicated: false

        };
    },
    methods: {
        idCheck() {
            console.log("=============================");
            console.log(this.usersVo.id);
            if (this.usersVo.id == "") {
                alert(" ")
            } else {
                console.log(this.usersVo);
                axios({
                    method: 'post', // put, post, delete
                    url: `${this.$store.state.apiBaseUrl}/api/user/idcheck`,
                    headers: { "Content-Type": "application/json; charset=utf-8" }, //
                    //params: userVo, //get   
                    data: this.usersVo, //put, post, delete   JSON  
                    responseType: 'json' //
                }).then(response => {
                    console.log(response.data); //

                    if (response.data == 0) {
                        alert(" .");
                        this.isNotDuplicated = true;
                        this.isDuplicated = false;
                        this.check = 1;
                    } else {
                        alert("  .")
                        this.isDuplicated = true;
                        this.isNotDuplicated = false;
                    }

                }).catch(error => {
                    console.log(error);
                });
            }
        },
        signup() {
            console.log(this.usersVo);
            this.usersVo.email = this.emailName + this.domain;
            axios({
                method: 'post', // put, post, delete 
                url: `${this.$store.state.apiBaseUrl}/api/user/join`,
                headers: { "Content-Type": "application/json; charset=utf-8" }, //
                //params: usersVo, //get   
                data: this.usersVo, //put, post, delete   JSON  
                responseType: 'json' //
            }).then(response => {
                console.log(response.data); //

                if (response.data == 1) {
                    alert(".  .");
                    console.log("-----------------------------------");
                    console.log(this.usersVo.userName);
                    console.log(this.usersVo.email)
                    this.$router.push({ path: '/loginsuccess', query: { name: this.usersVo.userName } });
                    console.log("-----------------------------------");
                } else {
                    alert(" .");
                }


            }).catch(error => {
                console.log(error);
            });

        }
    },
    
    created() { }
};
</script>