<template>
    <div class="wrap">
        <AppHeader />
        <!-- //header -->

        <div class="login-container">
            <div class="main-container">
                <div class="container">

                    <img class="loginLogo" src="@/assets/images/logo.png" alt="">

                    <div class="logoTitle">  !</div>
                </div>
                <div class="login-form">
                    <form v-on:submit.prevent="login" method="post">
                        <input type="text" placeholder="" v-model="usersVo.id">
                        <input type="password" placeholder="" v-model="usersVo.password">
                        <button type="submit" class="loginBtn"></button>
                        <router-link to="/loginform" class="signupBtn" v-on:click.prevent="logoutAndsignup"></router-link>
                    </form>
                </div>
                <!-- //login-form -->
            </div>
        </div>
        <AppFooter />
        <!--//footer-->
    </div>
</template>


<script>
import axios from 'axios';
import "@/assets/css/user/login.css"
import AppFooter from '@/components/AppFooter.vue';
import AppHeader from '@/components/AppHeader.vue';
export default {
    name: "LoginView",
    components: {
        AppFooter,
        AppHeader
    },
    data() {
        return {
            usersVo: {
                id: "",
                password: ""
            }
        };
    },
    methods: {
        login(){
            console.log("login");
            console.log(this.usersVo);
            axios({
                method: 'post', // put, post, delete
                url: `${this.$store.state.apiBaseUrl}/api/user/login`,
                headers: { "Content-Type": "application/json; charset=utf-8" }, //
                //params: usersVo, //get   
                data: this.usersVo, //put, post, delete   JSON  
                responseType: 'json' //
            }).then(response => {
                console.log(response.data); // authUser
                
                if(response.data.result == "success"){
                    //  
                    let authUser = response.data.apiData;

                    //token    
                    console.log(response.headers.authorization.split(" ")[1]);
                    //"Authorization Bearer dajofddjaoadfklh.adfafa"
                    const token = response.headers.authorization.split(" ")[1];

                    //vuex 
                    this.$store.commit("setAuthUser", authUser);
                    this.$store.commit("setToken", token);

                    console.log(authUser);
                    console.log(token);
                    console.log(authUser.userName);

                    ///
                    if (authUser.id === "admin" && authUser.password === "admin") {
                    alert(" .");
                    //     
                    this.$router.push("/manager");
                    } else {
                    //     
                    this.$router.push("/");
                    }

                
                } else {
                    console.log(response.data.message);
                    alert("  .");
                }
               

            }).catch(error => {
                console.log(error);
            });
        },
        
        /* login */

        logoutAndsignup(){
            console.log("");
            this.$store.commit("setAuthUser", null);
            this.$store.commit("setToken", null);
        }
    },
    created() { 
       
    }
};
</script>


<style></style>