<template>
    <div>
        <div class="wrap">
            <AppManagerHeader />
            <div class="managerHome">
                <div class="pManager">
                    <p></p>
                </div>
                <h3></h3>
                <div class="managerTitle">
                    <div class="title01">
                        <router-link to="" @click="handleAllClick"></router-link>
                        <router-link to="" v-for="(categoryVo, i) in categoryList" v-bind:key="i"
                            @click="handleClick(categoryVo.mcNo)"> {{ categoryVo.mcName }}</router-link>
                    </div>
                    
                    <div class="title02">
                        <p><span>&nbsp; {{ productList.length }}</span>
                        </p>
                    </div>
                </div>
                <div class="foodListGroup">
                    <div class="foodList" v-for="(productVo, i) in productList" v-bind:key="i">
                        <div class="foodImg">
                            <img v-bind:src="`${this.$store.state.apiBaseUrl}/upload/${productVo.saveName}`">
                            <!-- <img v-bind:src="`${this.$store.state.apiBaseUrl}/upload/${productVo.saveName}`"> -->
                        </div>
                        <div class="foodExplan">
                            <p class="foodName2">{{ productVo.productName }}</p>
                            <p class="foodEx2">{{ productVo.detail }}</p>
                            <p class="foodPrice2">{{ numberWithCommas(productVo.price) }}<span></span></p>
                            <div class="attribute2">
                                <span class="storageCold" v-if="productVo.storage == 1">&</span>
                                <span class="storageWarm" v-if="productVo.storage == 2"></span>
                                <img v-if="productVo.best == 1" src="@/assets/images/main/Best.png">
                            </div>
                        </div>
                    </div>


                </div>


            </div>
        </div>

    </div>
</template>

<script>
import '@/assets/css/manager/manager.css';
import AppManagerHeader from '@/components/AppManagerHeader.vue';
import axios from 'axios';
export default {
    name: "ManagerMainView",
    components: {
        AppManagerHeader
    },
    data() {
        return {
            categoryList: [],
            productList: [],
        };
    },
    methods: {
        numberWithCommas(x) {
            return x.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ',');
        },
        handleAllClick() {
            this.getsamttugiList();
        },
        getManagerCategory() {
            console.log("  ");

            axios({
                method: 'get', // put, post, delete 
                url: `${this.$store.state.apiBaseUrl}/api/managerCategory`,
                headers: { "Content-Type": "application/json; charset=utf-8" }, //
                responseType: 'json' //
            }).then(response => {
                console.log(response); //
                this.categoryList = response.data.apiData;
            }).catch(error => {
                console.log(error);
            });

        },
        getsamttugiList() {
            console.log(" ");
            axios({
                method: 'get', // put, post, delete 
                url: `${this.$store.state.apiBaseUrl}/api/manager`,
                headers: { "Content-Type": "application/json; charset=utf-8" }, //
                responseType: 'json' //
            }).then(response => {
                console.log(response); //
                this.productList = response.data.apiData;
            }).catch(error => {
                console.log(error);
            });

        },
        handleClick(i) {
            console.log(" " + i);
            axios({
                method: 'get', // put, post, delete 
                url: `${this.$store.state.apiBaseUrl}/api/getCategoryList`,
                headers: { "Content-Type": "application/json; charset=utf-8" }, //
                params: { mcNo: i }, //get   
                // data: guestbookVo, //put, post, delete   JSON  
                responseType: 'json' //
            }).then(response => {
                console.log(response.data.apiData); //
                this.productList = response.data.apiData;


            }).catch(error => {
                console.log(error);
            });


        }
    },
    created() {
        this.getManagerCategory();
        this.getsamttugiList();
    }
};
</script>

<style></style>