<template>
    <div>
        <div class="wrap">
            <!--  -->
            <AppHeader v-bind:menuNo="this.$route.params.no" />

            <!--  -->
            <div class="ItemListContainer">
                <div class="ItemListInner">
                    <!--  -->
                    <div class="channelSrc">
                        <router-link to="/"></router-link>
                        <router-link to="">{{ this.mcName }}</router-link>
                    </div>
                    <!--  -->
                    <!-- <h2>{{categoryList[this.$route.params.no].mcName}}</h2> -->
                    <h2>{{ this.mcName }}</h2>
                    <!--  -->
                    <div class="ItemListSection">
                        <!--  -->
                        <ul class="ItemTapMenu">
                            <li v-on:click="getItemAll(no)"><router-link to=""></router-link></li>
                            <li v-for="(sCategory, i) in scList" v-bind:key="i" v-on:click="getItemBySc(sCategory.scNo)" >
                                <router-link to="">{{ sCategory.scName }}</router-link>
                            </li>
                        </ul>
                        <!--  -->
                        <div class="ItemContentBoxes" >
                            <div class="ItemContentBox" v-for="(ItemVo,i) in itemList" v-bind:key="i">
                                <router-link v-bind:to="`/itemaccount/${ItemVo.productNo}`">
                                    <!--   -->
                                    <div class="ItemContentImg">
                                        <!-- <img src="@/assets/images/JinRamenSpicy.jpg"> -->
                                        <img v-bind:src="`${this.$store.state.apiBaseUrl}/upload/${ItemVo.saveName}`">
                                        <!-- <img v-bind:src="`${this.$store.state.apiBaseUrl}/upload/${this.$route.query.saveName}`"> -->
                                    </div>
                                    <!--   -->
                                    <div class="ItemContentText">
                                        <p>{{ ItemVo.productName }}</p>
                                        <p class="ItemPrice"><span>{{ ItemVo.price.toLocaleString() }}</span></p>
                                        <div class="ItemStorageMethod">
                                            <span v-if="ItemVo.storage == 1">&</span>
                                            <span v-if="ItemVo.storage == 2"></span>
                                            <span><img v-if="ItemVo.best == 1">Best</span>
                                        </div>
                                    </div>
                                </router-link>
                            </div>
                            
                        </div>

                    </div>

                </div>
            </div>

            <!--  -->
            <AppFooter/>
            
        </div>
    </div>
</template>
<script>
import axios from 'axios';
import '@/assets/css/item/itemList.css'
import AppHeader from '@/components/AppHeader.vue'
import AppFooter from '@/components/AppFooter.vue'
export default {
    name: "ItemList",
    components: {
        AppHeader,
        AppFooter
    },
    data() {
        return {
            scList:[],
            itemList:[],
            // iList:[]
            mcName:""
            
        };
    },
    methods: {

        //  
        getScList(no){
            
            console.log(" ");
            no= this.$route.params.no;
            // console.log(no);
            axios({
                method: 'get', // put, post, delete 
                url: `${this.$store.state.apiBaseUrl}/api/sclist`,
                headers: { "Content-Type": "application/json; charset=utf-8" }, //
                params: {no:no}, //get   
                // data: guestbookVo, //put, post, delete   JSON  
                responseType: 'json' //
            }).then(response => {
                console.log("",response.data.apiData); //
                this.scList = response.data.apiData.scList;
                this.mcName = response.data.apiData.mcName;

                console.log(this.scList);
                
            }).catch(error => {
                console.log(error);
            });

        },

        //   
        getItemAll(no){
            console.log("  !!")
            no= this.$route.params.no;
            // console.log(no);

            axios({
                method: 'get', // put, post, delete 
                url: `${this.$store.state.apiBaseUrl}/api/iListAll`,
                headers: { "Content-Type": "application/json; charset=utf-8" }, //
                params: {no:no}, //get   
                responseType: 'json' //
            }).then(response => {
                // console.log(response.data.apiData); //
                this.itemList = response.data.apiData;


                
            }).catch(error => {
                console.log(error);
            });

            
        },

        //   
        getItemBySc(i){
            const no= this.$route.params.no;
            console.log(" :"+i);
            console.log(" :"+ no);

            const NoVo = {
                scNo:i,
                mcNo:no
            }
            axios({
                method: 'get', // put, post, delete 
                url: `${this.$store.state.apiBaseUrl}/api/iListByscNo`,
                headers: { "Content-Type": "application/json; charset=utf-8" }, //
                params: NoVo, //get   
                responseType: 'json' //
            }).then(response => {
                //console.log(response.data.apiData); //
                this.itemList = response.data.apiData;


                
            }).catch(error => {
                console.log(error);
            });
        },


    },
    created(){
        const no = this.$route.params.no;
        this.getScList();
        this.getItemAll(no);
    },
};
</script>
<style></style>